.CDEK-widget {
  font: 16px/22px 'Noah', sans-serif;
  color: $color-general;

  &__sidebar {
    &-burger {
      hr {
        display: none;
      }
    }
  }

  &__delivery-type {
    top: calc(100% + 8px);
    background-color: transparent;
    overflow: hidden;

    &__button {
      width: 44px;
      flex: 0 0 auto;
      background-color: $button-bg !important;

      svg {
        width: 24px;
        stroke: #fff;
      }
    }

    &__title {
      padding: 19px 16px;
      text-align: left;
      font-size: 16px;
      line-height: 1;
      font-weight: 700;
      color: $color-general;
      background-color: #fff;
      border: 0;
      border-radius: 10px;
    }

    &__item {
      padding: 16px;
      margin-top: 8px;
      background-color: #fff;
      border-radius: 10px;

      &:last-of-type {
        border-radius: 10px;
      }

      &:hover, &.active {
        box-shadow: inset 0 0 49px 73px rgba(#B7A9D0, 0.15);
      }

      &-title {
        margin-bottom: 10px;

        svg {
          height: 20px;
          margin-right: 10px;
          stroke: #B7A9D0;
        }

        > span {
          color: $color-general;
          font-size: 16px;
          line-height: 1;
          font-weight: 700;
        }
      }

      &-details {
        padding-left: 34px;

        p {
          margin: 10px 0 0;
          font-size: 16px;
          line-height: 1;
        }
      }
    }
  }

  &__search {
    width: 285px;
    box-shadow: none;
    background-color: transparent;
    border-radius: 10px;

    &-box {
      height: 44px;
      overflow: hidden;
      border-radius: 10px;

      input {
        padding: 12px 20px;
        font: 18px/1 $font;
        font-weight: 400;
        color: #AD8BD7;
      }
    }
  }

  &__panel {
    background-color: #F7F4FD;
    $this: &;

    &-headline {
      width: auto;
      height: auto;
      margin: 0;
      padding: 20px 26px 12px;
      text-align: left;

      span {
        font-size: 24px;
        line-height: 1;
        font-weight: 700;
        color: $color-general;
      }
    }

    &-contacts {
      #{$this}-content {
        color: $color-general;

        p a {
          color: $color-general;

          svg .path {
            fill: $color-general;
          }
        }
      }
    }

    &-list {
      padding: 0 10px 0 0;

      &__item {
        width: auto;
        margin: 0 10px 10px;
        color: $color-general;
        background-color: #fff;
        border-radius: 10px;

        svg .svg-line {
          fill: #B7A9D0;
        }

        &-name {
          font-size: 16px;
          line-height: 1;
          font-weight: 700;
        }

        p {
          margin: 0 0 14px 23px;
          line-height: 1.37;

          &:last-child {
            margin-bottom: 0;
          }
        }
      }
    }

    &-details {
      &__back {
        width: 30px;

        svg {
          width: 8px;
          height: 15px;

          .path {
            fill: $color-general;
          }
        }

        &:hover {
          svg .path {
            fill: $button-bg;
          }
        }
      }

      &__block {
        margin: 0 0 18px;

        &-head {
          padding: 0 0 10px;
          font-size: 18px;
          line-height: 1;
          color: $color-general;
          font-weight: 700;
        }

        &-text {
          color: $color-general;
        }
      }
    }
  }

  &__sidebar {
    background-color: #F7F4FD;

    &-button {
      svg {
        fill: $button-bg;
      }

      &.active {
        background-color: $button-bg;

        svg {
          fill: #fff;
        }
      }

      &:not(.active):hover {
        background-color: #fff;
      }
    }

    &-burger {
      opacity: 1;

      span {
        background-color: $button-bg;
      }

      &.active {
        span {
          background-color: #fff;
        }
      }
    }
  }

  &__choose {
    padding: 18px 20px;
    font-family: $font;
    color: #fff;
    font-size: 20px;
    line-height: 1;
    font-weight: 700;
    background-color: $button-bg;
    border: 0;
    border-radius: 10px;
    transition: background-color .2s;

    &:after {
      display: none;
    }

    &:hover {
      background-color: $button-bg-hover;
    }
  }

  .mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar {
    margin: 0 0 0 auto;
    width: 4px;
    background-color: #B7A9D0;
  }
}

.cdek-map {
  min-width: 100%;
  height: 536px;
}

.cdek-info {
  width: 100%;
  margin: 18px 0 0;

  &--hidden {
    display: none;
  }

  &__table {
    padding: 26px;
    font-size: 24px;
    color: $color-general;
    border: 1px solid #DFD1F3;
    border-radius: 10px
  }

  &__address-block {
    max-width: 532px;
    margin: 18px 0 0;
  }

  &__row {
    display: flex;
    justify-content: space-between;
    font-weight: 700;
  }

  &__name {
    line-height: 1.25;
  }

  &__reset {
    display: flex;
    align-items: center;
    font-size: .67em;
    line-height: 1;
    color: #B7A9D0;
    background-color: transparent;
    border: 0;
    cursor: pointer;
    transition: color .2s;

    svg {
      width: 13px;
      height: 13px;
      margin: 0 9px 2px 0;
      fill: #B7A9D0;
      transition: fill .2s;
    }

    &:hover {
      color: $color-general;

      svg {
        fill: $color-general;
      }
    }
  }

  &__bottom {
    max-width: 394px;
    margin-top: 12px;
  }

  &__hours, &__address {
    font-size: .67em;
    line-height: 1.37;
  }

  &__address {
    margin: 0 0 12px;
  }

  &__hours {
    max-width: 36%;
    color: #B7A9D0;
  }
}

@media (max-width: 767px) {
  .cdek-map {
    height: 500px;
    width: calc(100% + 44px);
    margin: 0 -22px;
  }
  .cdek-info {
    &__row {
      flex-direction: column;
      justify-content: normal;
    }
  }
  .CDEK-widget {
    &__sidebar {
      width: 45px;

      &-button {
        height: 45px;
      }

      &-burger {
        padding: 13px 0 14px;
      }
    }

    &__panel {
      right: 45px;
      max-width: calc(100% - 45px);

      &-headline {
        padding: 15px 15px 10px;

        span {
          font-size: 18px;
        }
      }

      &-list {
        &__item {
          margin: 0 5px 5px;
          padding: 12px 16px;

          p {
            margin: 0 0 10px 23px;
            font-size: 14px;
            line-height: 1.2;
          }
        }
      }
    }

    &__search {
      width: 235px;
      top: 5px;
      left: 5px;

      &-box {
        input {
          padding: 12px;
        }
      }
    }
  }
}