// Шрифты
$font: 'Noah', sans-serif;

// Цвета
$color-primary: #122A49;
$color-general: #563494;
$color-bootstrap-dark: #0275D8;
$color-text: #373a3c;
$color-caption: #999999;
$color-placeholder: #757575;
$color-border: #DAE0E6;
//$color-bg: #F9F0FB;
$color-bg: transparent;
//$color-footer-bg: #563494;
$color-footer-bg: #29c9d9;
$color-main-menu-text: #fff;
$color-main-menu-hover: #FF1AC6;
$color-bg-sub: #fff;
$main-bg: #F9F0FB;
// UI-kit

// UI-kit Кнопки
$button-bg: #D118A3;
$button-bg-hover: #FF1AC6;
$button-bg-click: #99297D;
$button-bg-disabled: #D5C4CF;
$button-border-radius: 10px;

// UI-kit likes
$like-bg: #7149B7;
$like-stroke-hover: #7149B7;
$like-stroke: #AD8BD7;

//UI-kit header
$header-bg: radial-gradient(50% 7771.97% at 50% 50%, #29c9d9 20.31%, #563494 100%);


//UI-kit
$href-hover: #7149B7;
$href-color: #AD8BD7;

//UI-kit links
$color-link-hover: #FF1AC6;

//UI-kit
$color-button: #AD8BD7;



//$color-top-text: #B7A9D0;
$color-top-text: #fff;
$radius: 8px;

// Время
$time-xs: 100ms;
$time-sm: 200ms;
$time-md: 400ms;
$time-lg: 600ms;
$time-xl: 800ms;


// Отступы
$indent-lg: 1.5rem;
$indent-md: 1rem;
$indent-sm: 0.5rem;
$indent-xs: 0.25rem;
$indent-list: 20px;


$whiteZoomIcon: url(data:image/svg+xml;base64,);
